import React, {useState, useEffect} from "react";

export function Tabs({
  idTab,
  changeTab = () => {}
}) {

  const [buy, setBuy] = useState('');
  const [sell, setSell] = useState('');
  const [record, setRecord] = useState('');
  const [setting, setSetting] = useState('');

  useEffect(() => {
    const tmpClass = 'is--active'
    if (idTab === 'buy'){
      setBuy(tmpClass)}
    if (idTab === 'sell'){
      setSell(tmpClass)}
    if (idTab === 'record'){
      setRecord(tmpClass)}
    if (idTab === 'setting'){
      setSetting(tmpClass)}
  }, [idTab]);

  const handleTab = (idTmp) => {
    changeTab(idTmp);
  };

  return (
    <div className='o-wrapper'>
      <nav className='c-tabs'>

        <button className={'c-tabs__button ' + buy} onClick={handleTab('buy')}>
          Comprar</button>
        <button className={'c-tabs__button ' + sell} onClick={handleTab('sell')}>
          Vender</button>
        <button className={'c-tabs__button ' + record} onClick={handleTab('record')}>
          Historial</button>
        <button className={'c-tabs__button ' + setting} onClick={handleTab('setting')}>
          Configuración</button>

        <div className='c-tabs__statistics'>
          <img src="img/statistics.png" alt="statisticas" />
          <span>Analíticas</span>
        </div>

      </nav>
    </div>
  );
}
