import React from "react";
import { CoinTable } from "./StepsCoinTable";

export function Coin() {
  return (
    <div className='c-coin'>
      <div className='c-coin__coin'>
        <h5 className='c-coin__title'>
          Moneda</h5>
        <input className='c-coin__input' type="text" placeholder='USDT' />
      </div>

      <div className='c-coin__quantity'>
        <h5 className='c-coin__title'>
          Cantidad</h5>
        <input className='c-coin__input' type="text" placeholder='$100' />
      </div>

      <CoinTable />

    </div>
  );
}
