import React from "react";

export function VerifySell() {
  return (
    <div className='c-verifysell'>
        <p className='c-verifysell__paragraph'>
            Luego de realizar la transferenciaa nuestras cuentas, necesitamos verificar con el banco para poder enviar los USDT
        </p>
        <h4 className='c-verifysell__title'>
            Código / Número de operación
        </h4>
        
        <div className='c-verifysell__code'>
            <input className='c-verifysell__input' type="text" placeholder='34345' />
        </div>
    </div>
  );
}
