import React, { useState, Fragment } from "react";

import { Tabs as TabsComponent} from "./Tabs";
import { Steps } from "./Steps";

export function Content() {
  const [idTab, setIdTab] = useState('sell')

  const changeTab = (event,newId) => {
    setIdTab(newId)
  }

  return (
    <Fragment>
        <TabsComponent idTab={idTab} changeTab={changeTab} />
        <Steps/>
    </Fragment>
  );
}
