import React, {useState} from "react";

export function CoinTable() {
  const [checkStatus, setCheckStatus] = useState(false)

  const handleCheckChange = (event) => {
    setCheckStatus((prevTodos) => {
      return !prevTodos;
    });
  };

  return (
    <div className='c-stepscointable'>
      <h4 className='c-stepscointable__title'>
          Detalles de Compra
      </h4>

      <div className='c-stepscointable__action'>
        <h5 className='c-stepscointable__subtitle'>
          Pagar en soles
        </h5>
        {/* <div className='c-stepscointable__checkbox' > */}
        <button className={`c-stepscointable__checkbox ${checkStatus ? 'is--active' : ''}`} onClick={handleCheckChange} >
          <input type="checkbox" />
        </button>
      </div>
      
      <div className='c-stepscointable__content'>
          <p className='c-stepscointable__paragraph'>
              <span>Sub Total</span>
              <span>S./383</span>
          </p>
          <p className='c-stepscointable__paragraph'>
              <span>Tipo de cambio</span>
              <span>S./383</span>
          </p>
          <p className='c-stepscointable__paragraph'>
              <span>Comisión</span>
              <span>S./383</span>
          </p>
          <p className='c-stepscointable__paragraph'>
              <span>Total a transferir</span>
              <span>S./383</span>
          </p>
      </div>
    </div>
  );
}
