import React, { useState, useEffect} from "react";

export function Button({
  status = false,
  text='Botón',
  icon= ''
}) {

  const classText = 'c-button'

  const [image, setImage] = useState('')
  useEffect(() => {
    if (icon) {
      setImage(() => {
        return (
          <img className={classText + '__icon'} src={icon} alt='icon-button' />
        )
      })
    }
  }, [icon]);
  
  const [classes, setclasses] = useState(classText)
  useEffect(() => {
    if (status) {
      setclasses(() => {
        return classText + ' is--white'
      })
    } else {
      setclasses(() => {
        return classText
      })
    }
  }, [status]);

  return (

    <button className={classes}>
      {image}
      <span className='c-button__text'>
        {text}</span>
    </button>

  );
}