import React from "react";
import { Transfer } from "./StepsTransfer";
import { Coin } from "./StepsCoin";
import { Bank } from "./StepsBank";
import { VerifyBuy } from "./StepsVerifyBuy";
import { VerifySell } from "./StepsVerifySell";
import { VerifySummary } from "./StepsVerifySummary";
import { Button as ButtonComponent } from "./Button";

export function Steps() {
  return (
    <div className='o-wrapper'>
      <div className='c-steps'>

          <div className='c-steps__step'>
            <div className='c-steps__number is--finalized'>
              <span>✔</span></div>

            <h3 className='c-steps__title'>
              Cantidad y Moneda a Comprar</h3>
            
            <Coin />

            <div className='c-steps__buttons'>
              <ButtonComponent text='Atrás' status={true} />
              <ButtonComponent text='Siguiente' />
            </div>
          </div>

          <div className='c-steps__step'>
            <div className='c-steps__number is--enabled'>
              <span>2</span></div>

            <h3 className='c-steps__title'>
              Transferir</h3>
            <Transfer />

            <div className='c-steps__buttons'>
              <ButtonComponent text='Atrás' status={true} />
              <ButtonComponent text='Siguiente' />
            </div>

            <Bank></Bank>
            <div className='c-steps__buttons'>
              <ButtonComponent text='Atrás' status={true} />
              <ButtonComponent text='Siguiente' />
            </div>
          </div>

          <div className='c-steps__step'>
            <div className='c-steps__number is--disabled'>
              <span>3</span></div>

            <h3 className='c-steps__title'>
              Verificación</h3>
            
            <VerifyBuy></VerifyBuy>

            <div className='c-steps__buttons'>
              <ButtonComponent text='Atrás' status={true} />
              <ButtonComponent text='Terminar' />
            </div>

            <VerifySell></VerifySell>

            <div className='c-steps__buttons'>
              <ButtonComponent text='Atrás' status={true} />
              <ButtonComponent text='Terminar' />
            </div>

            <VerifySummary></VerifySummary>

            <p className='c-steps__paragraph'>
              Verificaremos tu operacion y estaremos transfiriendo a tu wallet</p>
            <div className='c-steps__buttons'>
              <ButtonComponent text='Resetear' status={true} />
            </div>

            <VerifySummary background={true} />

            <p className='c-steps__paragraph'>
              Verificaremos tu operacion y estaremos transfiriendo a tu wallet</p>
            <div className='c-steps__buttons'>
              <ButtonComponent text='Resetear' status={true} />
            </div>
          </div>

      </div>
    </div>
  );
}
